import React, { useEffect } from "react";
import "./Orders.scss";
import OrderLine from "./OrderLine/OrderLine";
import Pagination from "./Pagination/Pagination";
import { useSelector } from "react-redux";
import EntityFilter from "../Global/EntityFilter/EntityFilter";
import { useDispatch } from "react-redux";
import { getCities } from "../../redux/actions/cities";

const Orders = () => {
const {cities} = useSelector((state) => state.cities);
const {newOrder} = useSelector((state) => state.orders);
const dispatch = useDispatch();

useEffect(() => {
  dispatch(getCities());
}, [newOrder])
  return (
    <div className="orders">
      <div className="orders__container">
        <a className="orders__title">Заказы</a>
        <div className="orders__block">
        <EntityFilter filter={cities} placeholder="Город" />
          <div className="orders__line">
            <OrderLine />
          </div>
          <div className="orders__pagination">
            <Pagination />
          </div>
        </div>
      </div>
    </div>
  );
};

export default Orders;
